@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 700;
}

body {
  box-sizing: border-box;
  margin: 0 8px;
}

input {
  box-sizing: border-box;
  color: #3d4e61;
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  flex-shrink: 0;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  margin: 20px auto 0;
  font-family: Avenir, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.input_big {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.input_small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.input_big:hover, .input_small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.input_small:active, .input_big:active {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  box-shadow: 0 4px 4px #00000040;
}

.label {
  width: 100%;
  display: block;
  position: relative;
}

.label:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
}

.label_big:after {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.label_small:after {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

